/* Import fonts */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;700;900&family=Rajdhani:wght@300;400;500;600;700&display=swap');

:root {
  --bg-dark: #0a0a0f;
  --bg-panel: #12121a;
  --bg-card: #1a1a25;
  --accent-green: #00ff88;
  --accent-cyan: #00ccff;
  --accent-orange: #ff6b35;
  --accent-red: #ff3366;
  --text-primary: #ffffff;
  --text-dim: #666688;
  --border: #2a2a3a;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

html, body, #root {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
}

body {
  font-family: 'Rajdhani', sans-serif;
  background: var(--bg-dark);
  color: var(--text-primary);
}

/* ============================================
   START SCREEN
   ============================================ */
.App.start-screen {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background: 
    radial-gradient(ellipse at 50% 30%, rgba(0, 255, 136, 0.1) 0%, transparent 50%),
    var(--bg-dark);
}

.start-content {
  text-align: center;
  padding: 2rem;
}

.start-content h1 {
  font-family: 'Orbitron', sans-serif;
  font-size: 2rem;
  font-weight: 900;
  background: linear-gradient(135deg, var(--accent-green), var(--accent-cyan));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
}

.start-content > p {
  color: var(--text-dim);
  font-size: 1rem;
  margin-bottom: 2rem;
}

.start-btn {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  padding: 1.5rem 3rem;
  background: linear-gradient(135deg, var(--accent-green), var(--accent-cyan));
  color: var(--bg-dark);
  border: none;
  border-radius: 16px;
  cursor: pointer;
  letter-spacing: 0.1em;
  box-shadow: 0 0 40px rgba(0, 255, 136, 0.3);
  transition: all 0.2s;
}

.start-btn:active {
  transform: scale(0.95);
}

.start-instructions {
  margin-top: 2rem;
  color: var(--text-dim);
  font-size: 0.9rem;
}

.start-instructions p {
  margin: 0.5rem 0;
}

.start-instructions strong {
  color: var(--accent-cyan);
}

/* ============================================
   CONTROLLER LAYOUT (Landscape)
   ============================================ */
.App.controller-layout {
  display: flex;
  height: 100vh;
  width: 100vw;
  background: 
    radial-gradient(ellipse at 0% 50%, rgba(0, 255, 136, 0.05) 0%, transparent 50%),
    radial-gradient(ellipse at 100% 50%, rgba(255, 107, 53, 0.05) 0%, transparent 50%),
    var(--bg-dark);
}

/* Left Panel */
.left-panel {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 0.75rem;
  border-right: 1px solid var(--border);
}

/* Header */
.controller-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.controller-header h1 {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5rem;
  font-weight: 900;
  background: linear-gradient(135deg, var(--accent-green), var(--accent-cyan));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--accent-red);
  box-shadow: 0 0 10px var(--accent-red);
  animation: pulse 2s infinite;
}

.status-dot.connected {
  background: var(--accent-green);
  box-shadow: 0 0 10px var(--accent-green);
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Control Buttons */
.control-buttons {
  display: flex;
  gap: 0.5rem;
}

.ctrl-btn {
  flex: 1;
  padding: 0.75rem;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.7rem;
  font-weight: 700;
  border: 2px solid var(--accent-cyan);
  background: transparent;
  color: var(--accent-cyan);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  letter-spacing: 0.05em;
}

.ctrl-btn:active {
  transform: scale(0.95);
}

.ctrl-btn.connected {
  border-color: var(--accent-red);
  color: var(--accent-red);
}

.ctrl-btn.accel {
  border-color: var(--accent-green);
  color: var(--accent-green);
}

.ctrl-btn.accel.active {
  background: var(--accent-green);
  color: var(--bg-dark);
}

.ctrl-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

/* Servo Display */
.servo-display {
  background: var(--bg-card);
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid var(--border);
}

.servo-gauge {
  position: relative;
  height: 30px;
  margin-bottom: 0.5rem;
}

.gauge-track {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 8px;
  background: var(--bg-dark);
  border-radius: 4px;
  transform: translateY(-50%);
}

.gauge-indicator {
  position: absolute;
  top: 50%;
  width: 20px;
  height: 20px;
  background: linear-gradient(135deg, var(--accent-cyan), var(--accent-green));
  border-radius: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px var(--accent-cyan);
  transition: left 0.1s ease-out;
}

.gauge-labels {
  display: flex;
  justify-content: space-between;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.6rem;
  color: var(--text-dim);
  margin-top: 0.5rem;
}

.servo-value {
  text-align: center;
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--accent-cyan);
}

/* Mini Terminal */
.mini-terminal {
  flex: 1;
  background: var(--bg-dark);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 0.5rem;
  font-family: 'Courier New', monospace;
  font-size: 0.65rem;
  overflow: hidden;
  min-height: 60px;
}

.mini-terminal div {
  color: var(--text-dim);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.4;
}

.mini-terminal div.out {
  color: var(--accent-orange);
}

.mini-terminal div.in {
  color: var(--accent-cyan);
}

/* Instructions */
.instructions {
  font-size: 0.7rem;
  color: var(--text-dim);
  text-align: center;
}

/* Right Panel - Speed Slider */
.right-panel {
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background: var(--bg-panel);
}

.speed-label {
  font-family: 'Orbitron', sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  color: var(--accent-orange);
  letter-spacing: 0.1em;
  margin-bottom: 0.5rem;
}

.vertical-slider-container {
  flex: 1;
  position: relative;
  width: 60px;
  display: flex;
  justify-content: center;
}

.vertical-slider {
  writing-mode: vertical-lr;
  direction: rtl;
  width: 100%;
  height: 100%;
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
  position: absolute;
  z-index: 10;
}

.vertical-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 50px;
  height: 30px;
  background: linear-gradient(135deg, var(--accent-orange), #ff8855);
  border-radius: 8px;
  cursor: grab;
  box-shadow: 0 0 20px rgba(255, 107, 53, 0.5);
}

.vertical-slider::-webkit-slider-thumb:active {
  cursor: grabbing;
  transform: scale(1.1);
}

.vertical-slider::-moz-range-thumb {
  width: 50px;
  height: 30px;
  background: linear-gradient(135deg, var(--accent-orange), #ff8855);
  border-radius: 8px;
  cursor: grab;
  border: none;
  box-shadow: 0 0 20px rgba(255, 107, 53, 0.5);
}

.speed-track {
  position: absolute;
  bottom: 0;
  width: 30px;
  height: 100%;
  background: var(--bg-dark);
  border-radius: 15px;
  overflow: hidden;
  border: 2px solid var(--border);
}

.speed-fill {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: linear-gradient(to top, var(--accent-orange), var(--accent-red));
  border-radius: 12px;
  transition: height 0.1s ease-out;
}

.speed-value {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5rem;
  font-weight: 900;
  color: var(--accent-orange);
  margin: 0.5rem 0;
}

.stop-btn {
  width: 100%;
  padding: 0.75rem;
  font-family: 'Orbitron', sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  background: var(--accent-red);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  letter-spacing: 0.1em;
  box-shadow: 0 4px 15px rgba(255, 51, 102, 0.3);
}

.stop-btn:active {
  transform: scale(0.95);
  box-shadow: 0 2px 10px rgba(255, 51, 102, 0.5);
}

/* Scrollbar - hidden */
::-webkit-scrollbar {
  display: none;
}

/* Fullscreen adjustments */
:fullscreen .App,
:-webkit-full-screen .App,
:-moz-full-screen .App {
  width: 100vw;
  height: 100vh;
}
